<template>
  <b-row>
    <b-col sm="6">
      <b-card title="Upload Payment Details" class="iq-mb-3">
        <b-form-select
          plain
          v-model="selected"
          :options="options1"
          id="exampleFormControlSelect1"
        >
          <template v-slot:first>
            <b-form-select-option :value="null" disabled
              >Select</b-form-select-option
            >
          </template>
        </b-form-select>
        <div v-if="selected == 'bitcoin'">
          <b-form-group
            class="row mt-3"
            label-cols-sm="3"
            label="Bitcoin Wallet Key "
            label-for="bitcoin"
          >
            <b-form-input
              id="bitcoin"
              placeholder="Enter Your Bitcoin Key"
            ></b-form-input>
            <b-form-group
              class="mt-3"
              label-for="uploadFile"
              label="Payment Method Screenshot"
            >
              <b-form-file
                placeholder="Click/Drag Files To Upload"
                id="uploadFile"
              ></b-form-file>
            </b-form-group>
          </b-form-group>
        </div>
        <div v-if="selected == 'ethereum'">
          <b-form-group
            class="row mt-3"
            label-cols-sm="4"
            label="Ethereum Wallet Key "
            label-for="ethereum"
          >
            <b-form-input
              id="ethereum"
              placeholder="Enter Your Ethereum Key"
             ></b-form-input>
            </b-form-group>
            <b-form-group
              sm="12"
              class="mt-3"
              label-for="uploadFile"
              label="Payment Method Screenshot"
            >
              <b-form-file
                placeholder="Click/Drag Files To Upload"
                id="uploadFile"
              ></b-form-file>
            </b-form-group>
        </div>
         <div v-if="selected == 'bankaccount'">
          <b-form-group
            class="row mt-3"
            label-cols-sm="4"
            label="Bank Account Holder Name "
            label-for="holder"
          >
            <b-form-input
              id="holder"
             ></b-form-input>
            </b-form-group>
            <b-form-group
            class="row mt-3"
            label-cols-sm="4"
            label="IBAN "
            label-for="IBAN"
          >
            <b-form-input
              id="IBAN"
             ></b-form-input>
            </b-form-group>
            <b-form-group
            class="row mt-3"
            label-cols-sm="4"
            label="Recipient Bank Name "
            label-for="Recipient Bank Name"
          >
            <b-form-input
              id="Recipient Bank Name"
             ></b-form-input>
            </b-form-group>
            <b-form-group
            class="row mt-3"
            label-cols-sm="4"
            label="Recipient Bank Adress "
            label-for="Recipient Bank Adress"
          >
            <b-form-input
              id="Recipient Bank Adress"
             ></b-form-input>
            </b-form-group>
            <b-form-group
            class="row mt-3"
            label-cols-sm="4"
            label="Recipient Bank Swift"
            label-for="Recipient Bank Swift"
          >
            <b-form-input
              id="Recipient Bank Swift"
             ></b-form-input>
            </b-form-group>
            <b-form-group
            class="row mt-3"
            label-cols-sm="2"
            label="NOTE"
            label-for="note"
          >
             <b-form-textarea id="note" rows="3" sm="10"></b-form-textarea>
            </b-form-group>
        </div>
        <b-button href="#" block variant="primary" class="mt-3"
          >Upload</b-button
        >
      </b-card>
    </b-col>
    <b-col sm="6">
      <b-card title="Uploaded Payment Details" class="iq-mb-3">
        </b-card>
    </b-col>
  </b-row>
</template>
<script>
export default {
  data () {
    return {
      selected: '',
      options1: [
        { value: 'bitcoin', text: 'Bitcoin' },
        { value: 'ethereum', text: 'Ethereum' },
        { value: 'bankaccount', text: 'Bank Account' }
      ]
    }
  }
}
</script>
